const groups = (value, regex) => {

    let groups = [];
    let m = regex.exec(value);

    while (m != null) {
        groups.push(m[1]);
        m = regex.exec(value);
    }

    return groups;

};

export const encode = (value, types, i) => {

    if (i >= types.length) return value;

    const type = types[i];

    if (type === 'dict') {
        return JSON.parse(value);
    }

    if (type === 'tuple') {
        const values = groups(value, /\((.+?)\)/g);
        return values.map(it => encode(it, types, i + 1))[0];
    }

    if (['set', 'list'].includes(type)) {
        if (types[i + 1] === 'tuple') {
            const values = groups(value, /\((.+?)\)/g);
            return values.map(it => encode(it, types, i + 2));
        }
        const values = value.split(',');
        return values.map(it => encode(it, types, i + 1));
    }

    if (type === 'int') return parseInt(value);

    if (type === 'float') return parseFloat(value);

    if (type === 'str') return value;

    if (type === 'bool') return value === true || value === 'true';

    if (type.includes(',')) {
        const types = type.split(',').map(it => it.trim());
        return value.split(/[, ]+/).map(it => it.trim()).filter(it => it).map((it, i) => encode(it, [types[i]], 0));
    }

};


