import React from 'react';
import valid from './img/valid.png';
import invalid from './img/invalid.png';

const isValid = (value) => !Array.isArray(value) && value || value.length > 0;

const resultToDom = (result, key) => {

    console.log(result);

    if (key === 'status')
        return <img className="status" src={result ? valid : invalid}/>;

    if (key === 'columns' || key === 'intersection') {

        if (key === 'intersection')
            result = [result];

        return <div className="sardinas">
            {result.map((it, i) =>
                <div key={i} className="column">
                    {it.map(word => <span>{word}</span>)}
                </div>)
            }
        </div>;
    }

    if (key === 'pairs')
        return result.map((it, i) =>
            <div>
                <div key={i} className="part">({it.join(' , ')})</div>
                {i < result.length - 1 && <span className="comma">,</span>}
            </div>);

    if (Array.isArray(result)) {
        return result.map(it => <div key={it} className="part">{resultToDom(it)}</div>)
    }

    if (key === 'codebook') {
        return <div className='codebook'>
            {Object.entries(result).map(([key, value]) => <div><span>{key}</span><span>{value}</span></div>)}
        </div>;
    }

    if (result instanceof Object) {

        const rows = Object.entries(result).map(([key, value]) =>
            isValid(value) && <tr key={key}>
                <td>{key}</td>
                <td>{resultToDom(value, key)}</td>
            </tr>);

        return <table>
            <tbody>{rows}</tbody>
        </table>;
    }

    return <div className="res">{result}</div>

};

export default (props) => {

    const {result, time} = props.data;

    return <div className="result-container">
        <h4>Results</h4>
        <small className="time">{time}</small>
        <div className="result">
            {resultToDom(result)}
        </div>
    </div>

}



